.UI{
    margin-bottom: 112px;
    grid-column-start: 4;
    grid-column-end: 13;
    @media (max-width: 928px){
        grid-column-end: 9;           
    }
    @media (max-width: 672px){
        grid-column-start: 1;          
    } 
    @media (max-width: 450px){
        grid-column-end: 5;    
    }
}